import React from "react";
import axios from "axios";
import Movie from "./Movie";
import "./App.css";
import { Container } from "react-bootstrap";

class App extends React.Component {
  state = {
    isLoading: true,
    movies: []
  };

  //It will be wait for axios ..
  getMovies = async() => {
    const {
      data: {
        data: {movies}
      }
    } = await axios.get("https://yts-proxy.now.sh/list_movies.json?sort_by=rating");

    this.setState({movies, isLoading: false});
  }

  componentDidMount(){
    this.getMovies();

  }

  render(){
    const { isLoading, movies } = this.state;
    console.log(movies)
    return (
      <Container>
        {isLoading ? (
          <div className="loader">
            <span className="loader_text">Loading...</span>
          </div>
        ) : (
            <div className="movies">
              <div className="row">
                {movies.map(movie => (
                  <Movie
                    key={movie.id}
                    id={movie.id}
                    year={movie.year}
                    title={movie.title}
                    summary={movie.summary}
                    poster={movie.medium_cover_image}
                    genres={movie.genres}
                  />
                ))}
              </div>
            </div>
          )}
      </Container>
      // <section className="container">
      //   {isLoading ? (
      //     <div className="loader">
      //       <span className="loader_text">Loading...</span>
      //     </div>
      //   ) : (
      //     <div className="movies">
      //         <div className="row">
      //           {movies.map(movie => (
      //             <Movie
      //               key={movie.id}
      //               id={movie.id}
      //               year={movie.year}
      //               title={movie.title}
      //               summary={movie.summary}
      //               poster={movie.medium_cover_image}
      //               genres={movie.genres}
      //             />
      //           ))}
      //         </div>
      //     </div>
      //   )}
      // </section>
    );
  }
  
}

export default App;
